import { NgModule } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatCardModule } from '@angular/material/card';
import { MatIconModule } from '@angular/material/icon';
import { MatListModule } from '@angular/material/list';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatToolbarModule } from '@angular/material/toolbar';

import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule } from '@angular/router';
import { ScullyLibModule } from '@scullyio/ng-lib';
import { CookieLawModule } from 'angular2-cookie-law';
import { AppComponent } from './app.component';
import { FooterComponent } from './components/footer/footer.component';
import { NavbarComponent } from './components/navbar/navbar.component';

@NgModule({
  declarations: [AppComponent, NavbarComponent, FooterComponent],
  imports: [
    BrowserModule,
    ScullyLibModule,
    RouterModule.forRoot(
      [
        {
          path: 'kontakt',
          loadChildren: () =>
            import('@scheibentoenung-schmieder/contact').then(
              (m) => m.ContactModule
            ),
        },
        {
          path: 'folien',
          loadChildren: () =>
            import('@scheibentoenung-schmieder/folien').then(
              (m) => m.FolienModule
            ),
        },
        {
          path: '',
          pathMatch: 'full',
          loadChildren: () =>
            import('@scheibentoenung-schmieder/home').then((m) => m.HomeModule),
        },

        {
          path: '',
          loadChildren: () =>
            import('@scheibentoenung-schmieder/static-pages').then(
              (m) => m.StaticPagesModule
            ),
        },
      ],
      { anchorScrolling: 'enabled', scrollPositionRestoration: 'enabled' }
    ),
    BrowserAnimationsModule,
    MatToolbarModule,
    MatButtonModule,
    MatCardModule,
    MatIconModule,
    MatSidenavModule,
    MatListModule,
    CookieLawModule,
  ],
  providers: [],
  bootstrap: [AppComponent],
})
export class AppModule {}
