import { Injectable } from '@angular/core';
import {
  ActivatedRoute,
  ActivatedRouteSnapshot,
  Event,
  NavigationEnd,
  Router,
} from '@angular/router';
import { Observable, of } from 'rxjs';
import { filter, map, startWith, switchMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class RouterHelperService {
  constructor(private router: Router, private activatedRoute: ActivatedRoute) {}

  getDeepSnapshot(
    activatedRoute: ActivatedRoute = this.activatedRoute
  ): Observable<ActivatedRouteSnapshot> {
    return this.onNavigationEnd().pipe(
      switchMap(() => of(null)),
      startWith(this.getDeepCurrentSnapshot(activatedRoute.snapshot)),
      map(() => this.getDeepCurrentSnapshot(activatedRoute.snapshot))
    );
  }

  public onNavigationEnd(): Observable<NavigationEnd> {
    return this.getRouterEventByType<NavigationEnd>(NavigationEnd);
  }

  private getDeepCurrentSnapshot(
    activatedRouteSnapshot: ActivatedRouteSnapshot
  ): ActivatedRouteSnapshot {
    if (activatedRouteSnapshot.firstChild) {
      return this.getDeepCurrentSnapshot(activatedRouteSnapshot.firstChild);
    }
    return activatedRouteSnapshot;
  }

  private getRouterEventByType<T extends Event>(
    eventType: Function
  ): Observable<T> {
    return <Observable<T>>(
      this.router.events.pipe(filter((event) => event instanceof eventType))
    );
  }
}
