import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { NavigationEnd, Router } from '@angular/router';
import { ScullyRoutesService } from '@scullyio/ng-lib';
import { iif, of } from 'rxjs';
import { filter, map, shareReplay, switchMap } from 'rxjs/operators';
import { RouterHelperService } from './services/router-helper.service';

@Component({
  selector: 'scheibentoenung-schmieder-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  data$ = this.routerHelperService
    .getDeepSnapshot()
    .pipe(map(({ data }) => data));

  meta$ = this.router.events.pipe(
    filter((event) => event instanceof NavigationEnd),
    switchMap(() => this.data$),
    switchMap((data) =>
      iif(() => data.markdown, this.scully.getCurrent(), of(data))
    ),
    shareReplay(1)
  );

  constructor(
    private router: Router,
    private scully: ScullyRoutesService,
    public routerHelperService: RouterHelperService,
    private titleService: Title,
    private metaService: Meta
  ) {}

  ngOnInit() {
    this.meta$.subscribe(({ title, description, cover }) => {
      this.titleService.setTitle(title);
      this.metaService.updateTag({ name: 'description', content: description });
      this.metaService.updateTag({
        property: 'og:description',
        content: description,
      });
      this.metaService.updateTag({
        property: 'og:image',
        content: '/assets/img/cover/' + cover,
      });

      this.metaService.updateTag({ property: 'og:title', content: title });
    });
  }
}
